<script>
  import Tooltip from './Tooltip.svelte';
  import Lock from './Lock.svelte';
  import {fade} from 'svelte/transition';
  import {download} from './download.js';
  import {size, grad, diaper, shield, background, scale, border, borderWidth, matrix, state, changes} from './stores';
  import {shields} from './dataModel';
  import {rw} from './utils';

  const sizes = [[80, "Giant"], [100, "Huge"], [150, "Large"], [200, "Medium"], [300, "Small"], [400, "Tiny"]];
  const gradients = ["luster", "spotlight", "backlight"];
  const diapers = ["nourse", "tessellation", "sennwald", "sulzbach"];
  const wideScreen = window.innerWidth > 600;

  $: position = $changes[1];
  // save options on change
  $: lock("size", $size);
  $: lock("grad", $grad);
  $: lock("diaper", $diaper);
  $: lock("shield", $shield);
  $: lock("background", $background);
  $: lock("scale", $scale);
  $: lock("border", $border);
  $: lock("borderWidth", $borderWidth);

  // don't lock options on load
  const loaded = {};
  function lock(key, value) {
    if (loaded[key]) {
      localStorage.setItem(key, value);
    } else loaded[key] = true;
  }

  function getRandomColor() {
    const l = '0123456789ABCDEF';
    $background = "#"+[0,0,0,0,0,0].map(() => l[Math.floor(Math.random() * 16)]).join("");
  }

  function getPath(shield) {
    return document.getElementById(shield).innerHTML;
  }

  function getIcon(icon, active = "active") {
    if (wideScreen) return icon;
    return `<svg class="navBarIcon ${active}"><use href="#${icon}-icon"></use></svg>`;
  }

  function reroll() {
    $matrix += 1;

    // change shield if not manually selected
    if (!localStorage.getItem("shield")) {
      $shield = rw(shields[rw(shields.types)]);
      loaded.shield = false;
    }
  }
</script>

<div id="navbar">
  <ul>
    <svg class=logo width=35 height=35 viewBox="-2 -1 55 55">
      <title>Armoria: Heraldry Generator</title>
      <path fill="#fff" stroke="none" d="m 46,3 0.6,1.4 c -1.5,0.7 -2.6,1.4 -3.3,2.2 -0.7,0.7 -1.2,1.5 -1.5,2.4 -0.3,0.9 -0.4,2.2 -0.4,3.9 0,0.6 0,1.3 0,2.2 l 0.5,23.2 c 0,2.5 0.3,4.2 0.8,5 0.4,0.6 0.8,0.8 1.3,0.8 0.6,0 1.5,-0.6 2.8,-1.8 l 0.9,1.1 -5.8,4.9 -1.9,1.6 C 38.4,49.2 37.2,48.2 36.5,46.9 35.8,45.7 35.3,36.7 35.2,34 c -7.6,0.1 -20.2,0 -20.2,0 0,0 -7.4,9.1 -7.4,11.1 0,0.6 0.2,1.1 0.6,1.8 0.5,0.9 0.8,1.4 0.8,1.7 0,0.4 -0.1,0.7 -0.4,1 -0.3,0.3 -0.6,0.4 -1.1,0.4 -0.5,0 -0.9,-0.2 -1.2,-0.6 -0.5,-0.6 -0.7,-1.3 -0.7,-2.2 0,-1 0.3,-2.1 0.8,-3.3 C 8.8,39.9 11.3,36.7 14.1,32.9 11.1,30.7 9,28.5 7.8,26.4 6.6,24.4 6,22.1 6,19.5 c 0,-3 0.8,-5.7 2.3,-8.3 1.5,-2.5 3.8,-4.5 6.9,-6 3.1,-1.5 6.2,-2.2 9.4,-2.2 4.9,0 9.7,1.7 14.3,5.1 1.1,-1.2 2.2,-2.1 3.2,-2.9 1,-0.8 2.4,-1.5 4,-2.3 z M 30.7,10.2 c -2.6,-1.3 -5.2,-1.9 -7.8,-1.9 -2.7,0 -5.3,0.6 -7.8,1.8 -2.4,1.2 -4.2,2.8 -5.4,4.7 -1.2,1.9 -1.8,3.9 -1.8,5.9 0,4.2 2.3,8 6.9,11.3 L 25.2,17.7 c -1.6,-0.8 -2.9,-1.3 -4.2,-1.3 -1.7,0 -3.1,0.8 -4.2,2.4 -0.4,0.7 -1,0.5 -1.1,-0.2 0,-0.6 0.3,-1.5 1,-2.7 0.7,-1.1 1.6,-2.1 2.9,-2.8 1.3,-0.7 2.6,-1.1 4,-1.1 1.4,0 3.1,0.4 4.9,1.1 z m 4,3.2 C 34,12.4 32.8,11.5 32,11 L 18.4,29.4 h 16.7 z"/>
    </svg>

    <div class="container">
      <bl>{@html getIcon("options")}</bl>

      <div class="dropdown level1">
        <div class="container">
          <div class="dropdown level2">
            {#each Object.keys(shields.types) as type}
              <div class="container">
                <div class="dropdown level3 iconed">
                  {#each Object.keys(shields[type]) as sh}
                    <bt on:click={() => $shield = sh}>
                      <svg class=shield class:selected={sh === $shield} width=26 height=26 viewBox="0 0 200 210">{@html getPath(sh)}</svg>
                      {sh.split(/(?=[A-Z])/).join(" ")}
                    </bt>
                  {/each}
                </div>

                <bl>
                  {type.split(/(?=[A-Z])/).join(" ")}
                </bl>
              </div>
            {/each}
          </div>
          <bl>
            <Lock key=shield/>
            <Tooltip tip="Shield or banner form. Some forms do not work well with auto-generated heralrdy">Shield</Tooltip>
          </bl>
        </div>

        <bt on:click={() => $state.tinctures = 1}>
          <Lock key=tinctures/>
          <Lock key=colors/>
          <Tooltip tip="Define tinctures. Edit COA to change tincture itself">Tinctures</Tooltip>
        </bt>

        <div class="container">
          <div class="dropdown level2">
            <bt class:selected={!$grad} on:click={() => $grad = null}>No gradient</bt>
            {#each gradients as g}
              <bt class:selected={g === $grad} on:click={() => $grad = g}>{g}</bt>
            {/each}
          </div>
          <bl>
            <Lock key=grad/>
            <Tooltip tip="Gradient (overlay) style to be applied over coat of arms">Gradient</Tooltip>
          </bl>
        </div>

        <div class="container">
          <div class="dropdown level2">
            <bt class:selected={!$diaper} on:click={() => $diaper = null}>No pattern</bt>
            {#each diapers as d}
              <bt class:selected={d === $diaper} on:click={() => $diaper = d}>{d}</bt>
            {/each}
          </div>
          <bl>
            <Lock key=diaper/>
            <Tooltip tip="Diaper (subtle backing on coat of arms) style">Damasking</Tooltip></bl>
        </div>

        <div class="container">
          <div class="dropdown level2">
            {#each sizes as s}
              <bt class:selected={$size == s[0]} on:click={() => $size = s[0]}>{s[1]}</bt>
            {/each}
          </div>
          <bl>
            <Lock key=size/>
            <Tooltip tip="Gallery size">Gallery</Tooltip>
          </bl>
        </div>

        <div class="container">
          <div class="dropdown level2">
            <bl>Color
              {#if $border !== "#333333"}
                <Tooltip tip="Restore default color">
                  <svg on:click={() => $border = "#333333"} class="navBarIcon active smaller"><use href="#undo-icon"></use></svg>
                </Tooltip>
              {/if}
              <input type="color" bind:value={$border}/>
            </bl>
            <bl>Width
              <input class="right" type="number" min=0 max=4 step=.1 bind:value={$borderWidth}/>
            </bl>
          </div>
          <bl>
            <Lock key=border/>
            <Tooltip tip="Border style for coat of arms">Border</Tooltip>
          </bl>
        </div>

        <div class="container">
          <div class="dropdown level2">
            <bl>Color
              <Tooltip tip="Select random color">
                <svg on:click={getRandomColor} class="navBarIcon active smaller"><use href="#random-icon"></use></svg>
              </Tooltip>
              {#if $background !== "#333333"}
                <Tooltip tip="Restore default color">
                  <svg on:click={() => $background = "#333333"} class="navBarIcon active smaller"><use href="#undo-icon"></use></svg>
                </Tooltip>
              {/if}
              <input type="color" bind:value={$background}/>
            </bl>
          </div>
          <bl>
            <Lock key=background/>
            <Tooltip tip="Background color">Background</Tooltip>
          </bl>
        </div>

        <div class="container">
          <div class="dropdown level2">
            <bl class="wide">
              <input type="range" min=1 max=4 step=.1 bind:value={$scale}/>
              <input type="number" min=1 max=4 step=.1 bind:value={$scale}/>
            </bl>
          </div>
          <bl>
            <Lock key=scale/>
            <Tooltip tip="Downloaded image size, 1 is default size, 2 - 2x size, etc.">Scale</Tooltip>
          </bl>
        </div>
      </div>
    </div>

    {#if $matrix}
      <bt on:click={() => $matrix -= 1}>
        <Tooltip tip="Roll to the previous list. Hotkey: Backspace">{@html getIcon("rollback")}</Tooltip>
      </bt>
    {:else}
      <bd>{@html getIcon("rollback", "inactive")}</bd>
    {/if}

    <bt on:click={reroll}>
      <Tooltip tip="Regenerate coat of arms. Hotkey: Enter">{@html getIcon("reroll")}</Tooltip>
    </bt>

    <bt on:click={() => download()}>
      <Tooltip tip="Download png image. Set size in options. Hotkey: D">{@html getIcon("download")}</Tooltip>
    </bt>

    <div class="container"><bl>{@html getIcon("upload")}</bl>
      <div class="dropdown level1">
        <bt on:click={() => $state.raster = 1}>
          <Tooltip tip="Upload raster charge (one color, quality loss on scale) from jpg, png or svg image" left>Raster</Tooltip>
        </bt>

        <bt on:click={() => $state.vector = 1}>
          <Tooltip tip="Upload vector charge (multicolor and lossless scalable) from prepared svg" left>Vector</Tooltip>
        </bt>
      </div>
    </div>

    {#if $state.edit}
      {#if position > 0}
        <bt on:click={() => changes.undo()}>
          <Tooltip tip="Revert the latest change. Hotkey: Z">{@html getIcon("undo")}</Tooltip>
        </bt>
      {:else}
        <bd>{@html getIcon("undo", "inactive")}</bd>
      {/if}

      {#if position < changes.length() - 1}
        <bt on:click={() => changes.redo()}>
          <Tooltip tip="Restore next change. Hotkey: X">{@html getIcon("redo")}</Tooltip>
        </bt>
      {:else}
        <bd>{@html getIcon("redo", "inactive")}</bd>
      {/if}
    {/if}

    {#if $state.edit}
      <bt id="back" on:click={() => $state.edit = 0} transition:fade>
        <Tooltip tip="Get back to Gallery" left>{@html getIcon("back")}</Tooltip>
      </bt>
    {/if}

    {#if wideScreen || !$state.edit}
      <bt on:click={() => $state.about = 1}>
        <Tooltip tip="Show about screen. Hotkey: F1" left>{@html getIcon("about")}</Tooltip>
      </bt>
    {/if}
  </ul>
</div>

<style>
  ul {
    top: 0;
    width: 100%;
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    overflow: hidden;
    background-color: #1b1c1d;
    z-index: 1;
    white-space: nowrap;
  }

  .logo {
    margin: 0 .5em 0 1em;
    background-color: #35bdb2;
  }

  :global(.navBarIcon) {
    width: 1em;
    height: 1em;
    fill: #333;
    stroke: none;
    vertical-align: middle;
  }

  :global(.navBarIcon.active) {
    fill: #fff;
    cursor: pointer;
  }

  .navBarIcon.smaller {
    width: .8em;
    height: .8em;
  }

  .navBarIcon.smaller:active {
    transform: translateY(1px);
  }

  bt, bl, bd {
    user-select: none;
    padding: 1em 1.14em;
    line-height: 1.24;
    color: #fff;
    text-transform: capitalize;
  }

  bt {
    cursor: pointer;
    transition: background-color .1s;
  }

  bt:hover, bl:hover {
    background-color: #2d2e2f;
  }

  bt:active:not(:last-child) {
    transform: translateY(1px);
  }

  input[type="color"] {
    position: absolute;
    padding: 0;
    margin: -.3em 0;
    right: 1em;
    border: 0;
    width: 3em;
    cursor: pointer;
  }

  input[type="range"] {
    padding: 0;
    width: 72%;
    margin: 0;
    height: .6em;
    cursor: pointer;
  }

  input[type="number"] {
    padding: 0 0 0 3px;
    width: 25%;
    margin: 0;
    border: 0;
  }

  input[type="number"].right {
    position: absolute;
    padding: 2px;
    margin: -.25em 0;
    right: 1em;
    width: 3em;
  }

  .dropdown {
    display: none;
    position: fixed;
    background-color: #1b1c1d;
    min-width: 9em;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }

  .level1 {
    margin-top: 1em;
  }

  .level2 {
    z-index: 1;
    margin-left: 10.25em;
  }

  .level3 {
    z-index: 2;
    margin-left: 10.25em;
  }

  .dropdown bt, .dropdown bl {
    padding: 12px 16px;
    width: 8em;
    display: block;
  }

  .dropdown bl.wide {
    width: 12em;
  }

  .container:hover > .dropdown {
    display: block;
  }

  .dropdown bt.selected:before {
    content: '\2713';
    display: inline-block;
    padding: 0 6px 0 0;
  }

  @media only screen and (max-width: 640px), (max-height: 640px) {
    .dropdown bt, .dropdown bl {
      padding: 6px 16px;
      width: 7em;
    }

    .level2 {
      margin-left: 9.25em;
    }

    .level3 {
      margin-left: 9.25em;
    }
  }

  @media only screen and (max-width: 720px) {
    .logo {display: none};
  }

  @media only screen and (max-height: 640px) and (min-width: 520px) and (orientation: landscape) {
    .level3 {column-count: 2;}
  }

  @media only screen and (max-height: 640px) and (min-width: 680px) and (orientation: landscape) {
    .level3 {column-count: 3;}
  }

  .dropdown.iconed {
    text-indent: 1.8em;
  }

  .shield {
    position: absolute;
    fill: none;
    stroke: #fff;
    stroke-width: 5px;
    margin: -0.4em 0 0 -2.2em;
  }

  .shield.selected {
    fill: #777;
    stroke: #333;
  }

  #back {
    position: absolute;
    right: 0;
  }
</style>